export default {
	data(){
		return {
		}
	},
	methods:{
		linkDetails(id,storeid){
			// console.log(storeid)
			//当点击的时候跳转到详情页
			//根据index和我的组件中传过来的url 判断跳到哪里
				uni.navigateTo({
					url:`/pages/Details/Details?id=${id}&storeid=${storeid}`
				})
		},
		deletescollectionAndfootprint(index){
			let deleteid = this.deletelist[index].fav_id?this.deletelist[index].fav_id:this.deletelist[index].track_id
			uni.showModal({
				title:"确定要删除该商品吗",
				cancelText:true,
				cancelText:"确认取消",
				cancelColor:"#ff0000",
				confirmText:"确认删除",
				success:(res)=>{
					if(res.confirm){
						uni.request({
							url:this.deleteurl,
							method:"POST",
							data:{
								token:this.tokey,
								fav_id:deleteid,
								track_id:deleteid
							},
							success(res) {
								console.log(res)
								if(res.data.code==0){//这后期需要更改
									uni.switchTab({
										url:"/pages/PersonalMy/PersonalMy"
									})
								}
							}
						})
					}else{
						return false
					}
				}
			})
		}
	},
	props:["list","display","deleteurl","tokey","deletelist"]
}
